// apply stylesheet
import "./App.less";

// registered plugins
import "@pixi/tilemap";
import "@pixi/layers";
import "pixi-projection";

import { Stage } from "@pixi/layers";
import {
  AbstractRenderer,
  Application,
  DisplayObject,
  Loader,
  Sprite,
  Texture,
} from "pixi.js";
import { Component } from "./Component";
import { LoadingAnimation, Scene, SceneRoot } from "./Scene";
import { Interaction } from "./Interaction";

class Canvas extends Component {
  public readonly app: Application;

  constructor(parent: HTMLElement, width: number, height: number) {
    super();

    this.layout = { width, height };

    this.app = new Application({
      width,
      height,
      antialias: true,
      autoDensity: true,
      autoStart: false,
      backgroundAlpha: 0,
    });
    parent.appendChild(this.app.view);

    this.app.stage = new Stage();
    this.app.stage.addChild(this.ui);
    this.app.stage.addChild(this.orig);
  }
}

class SceneRootImpl implements SceneRoot {
  public readonly canvas: Canvas;
  public readonly parent: HTMLElement;

  // public currScene?: Scene;
  private readonly scenes = new Set<Scene>();

  constructor(parent: HTMLElement, width: number, height: number) {
    const div = document.createElement("div");
    div.classList.add("fill-parent");
    parent.appendChild(div);
    this.canvas = new Canvas(div, width, height);
    this.parent = div;
  }

  toggle(scene: Scene) {
    if (!this.scenes.has(scene)) {
      scene.mount(this);
      this.scenes.add(scene);
    }
    // this.currScene = scene;
    this.scenes.forEach((e) => e.toggleVisibility(e === scene));
  }
}

class App_0 {
  public readonly args = new URL(window.location.href).searchParams;
  public readonly renderer: AbstractRenderer;
  public readonly stage: DisplayObject;

  private readonly root: HTMLElement;
  private readonly bg: SceneRootImpl;
  private readonly fg: SceneRootImpl;

  private started: boolean = false;

  constructor() {
    const width = parseInt(this.args.get("width")) || 1920;
    const height = parseInt(this.args.get("height")) || 1080;
    console.log(width, height);

    this.root = document.createElement("div");
    this.root.classList.add("fill-parent");
    document.body.appendChild(this.root);

    this.bg = new SceneRootImpl(this.root, width, height);
    this.renderer = this.bg.canvas.app.renderer;
    this.stage = this.bg.canvas.app.stage;

    const hitbox = new Sprite();
    hitbox.width = width;
    hitbox.height = height;
    this.bg.canvas.app.stage.addChild(hitbox);
    new Interaction(
      this.renderer.plugins.interaction,
      hitbox,
      this.bg.canvas.ui
    );

    this.fg = new SceneRootImpl(this.root, width, height);
    this.fg.parent.classList.add("overlay");

    (<any>window).app = this;
  }

  texture(path: string): Texture {
    return Loader.shared.resources[path].texture;
  }

  run(scene: Scene) {
    this.initTicker();
    this.bg.toggle(scene);
    scene.run();
  }

  async wait<T>(anim: LoadingAnimation, payload: () => Promise<T>): Promise<T> {
    this.initTicker();
    this.fg.parent.classList.remove("overlay");
    this.fg.toggle(anim);
    const v = await anim.play(payload);
    anim.toggleVisibility(false);
    this.fg.parent.classList.add("overlay");
    return v;
  }

  private initTicker() {
    if (!this.started) {
      for (const { canvas } of [this.fg, this.bg]) {
        canvas.app.start();
      }
      this.started = true;
    }
  }
}

export const App = new App_0();
